import Vue from 'vue'
import VueRouter from 'vue-router'

const Home = () => import('../views/home/Home.vue')
const Core = () => import('../views/core/Core.vue')
const Categroy = () => import('../views/categroy/Categroy.vue')
const Profile = () => import('../views/profile/Profile.vue')
Vue.use(VueRouter);
const routes = [{
    path: '',
    redirect: '/home',
  },
  {
    path: '/core',
    component: Core,
    meta: {
      title:"Bug",
    }
  },
  {
    path: '/home',
    component: Home,
    meta: {
      title: "Home",
    }
  },
  {
    path: '/categroy',
    component: Categroy,
    meta: {
      title: "Bug",
    }
  },
  {
    path: '/profile',
    component: Profile,
    meta: {
      title: "ATL",
    }
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router