<template>
  <div id="tab-bar-item" @click="isActiveclick">
    <div v-if="!isActive">
      <slot name="item-icon" class="itemicon"></slot>
    </div>
    <div v-else>
      <slot name="item-icon-active" class="itemicon"></slot>
    </div>
    <div
      class="itemtext"
      :style="activeStyle"
    >
      <slot name="item-text"></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: "TabBarItem",
  props: {
    path: String,
    fontColor: {
      type: String,
      default: "red",
    },
  },
  data() {
    return {
      // isActive:false,
    };
  },
  computed: {
    isActive() {
      return this.$route.path.indexOf(this.path) !== -1;
    },
    activeStyle(){
      return this.isActive?{color:this.fontColor}:{};
    }
  },
  methods: {
    isActiveclick() {
      console.log("112");
      this.$router.replace(this.path);
    },
  },
};
</script>

<style scoped>
#tab-bar-item {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.itemicon,
.itemtext {
  flex: 1;
}

/* .active {
  color: red;
} */
</style>